import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import syntax from './components/syntax.vue'
import printing from './components/printing.vue'
import home from './components/home.vue'


Vue.use(VueRouter);

Vue.config.productionTip = false

const router = new VueRouter ({
  routes: [
   
    {path: '/', component: home},
    {path: '/syntax', component: syntax},
    {path: '/printing', component: printing}
    
    
  ],
  mode: 'history',
  scrollBehavior () {
    window.scrollTo(0,0);
  }
});

new Vue({
  render: h => h(App),
  router,
}).$mount('#app')